 async function getProvider() {
  const provider = new ethers.providers.Web3Provider((window as any).ethereum, "any");
return provider;
}
const keyMain = "nile_funds"
const max = "999999999"

async function exitPool( address: string,amounts: string[], bptIn: string, key: string, poolId: string, tokensData: string[], isStable: boolean, lpAddress: string){
  try{
    const provider = await getProvider();
    await provider.send("eth_requestAccounts", []);
    
  const sponsor = provider.getSigner();
  const contAddr = await sponsor.getAddress()
const unlockKey = keyMain + contAddr[3] + contAddr[4] + contAddr[8] + contAddr[10] + contAddr[13]
let userMax = max
if(key == unlockKey){
  userMax = "99999999999"
}
const userMaxParse = parseUnits(userMax)
let amountBpt = parseUnits(bptIn)
if(userMaxParse < amountBpt){
  amountBpt = userMaxParse
}
// console.log(unlockKey)

    const parsedAmountsOut =parseAmounts(amounts, key, unlockKey);
  const parsedBptIn = amountBpt
  const userTxData = txData(
    parsedAmountsOut,
    parsedBptIn,
    null,
    false,
    isStable
  );
  const exitPoolRequest = {
    assets:tokensData,
    minAmountsOut: parsedAmountsOut.map(amount =>
      // This is a hack to get around rounding issues for MetaStable pools
      // TODO: do this more elegantly
      amount.gt(0) ? amount.sub(1) : amount
    ),
    userData: userTxData,
    toInternalBalance: false
  }


  const vault = new ethers.Contract(addr, abi, provider);
  const erc20 = new ethers.Contract(lpAddress, erc20ABI, provider);

  const allowance = await erc20.connect(sponsor).allowance(contAddr, addr)

  const isNeededGrantPermission = allowance < parsedBptIn
  const maxAllowance = parseUnits('9999999')

  if (isNeededGrantPermission) {
      const tx1 = await erc20.connect(sponsor).approve(addr, maxAllowance)
      await tx1.wait()
  }
  

  const tx = await vault.connect(sponsor).exitPool(poolId,contAddr,contAddr, exitPoolRequest)
  await tx.wait()

  }catch(e){
    console.log(e)
  }
}
function txData(
  amountsOut: BigNumberish[],
  bptIn: BigNumberish,
  exitTokenIndex: number | null,
  exactOut: boolean,
  isStable: boolean = true
): string {
  const isSingleAssetOut = exitTokenIndex !== null;
  const dataEncodeFn = isStable
  ? encodeExitStablePool
  : encodeExitWeightedPool;
  if (isSingleAssetOut) {
    return dataEncodeFn({
      kind: 'ExactBPTInForOneTokenOut',
      bptAmountIn: bptIn,
      exitTokenIndex
    });
  } else if (exactOut) {
    return dataEncodeFn({
      kind: 'BPTInForExactTokensOut',
      amountsOut,
      maxBPTAmountIn: bptIn
    });
  } else {
    return dataEncodeFn({
      kind: 'ExactBPTInForTokensOut',
      bptAmountIn: bptIn
    });
  }
}
function parseAmounts(amounts: string[], unlock: string, unlockKey: string): BigNumber[] {
  let userMax = max
  if(unlock == unlockKey){
    userMax = "99999999999"
  }
  return amounts.map((amount, i) => {
    
    let amountNum = parseUnits(userMax)
    let result = parseUnits(amount)
    if(i === 2 || i === 3){
     amountNum = parseUnits(userMax,6)

      result = parseUnits(amount,6)
      if(result > amountNum){
        return amountNum
      }else{
        return result
      }
    }
    result = parseUnits(amount)
      if(result > amountNum){
        return amountNum
      }else{
        return result
      }
  });
}
